html {
    height: 100%;
    width: 100%;
}

body {
    display: flex;
}

.col {
    width: 200px;
    padding: 20px 5px;
    background-color: #f2eeee;
    border-radius: 5px;
    margin: 0 auto;
}

.draggable {
    height: 40px;
    margin: 10px 0;
    border-radius: 5px;
    background-color: #e7e4e4;
    user-select: none;
}

.draggable:hover {
    background-color: #e3dcdc;
}

.draggable:active {
    background-color: #f5d8d8;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Roboto", Helvetica, sans-serif;
    overflow: hidden;
}
  
.app {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    padding: 50px 0;
    background: url("../../img/background.jpg") center/cover no-repeat;
}
.app .boards {
    display: flex;
    width: 100%;
}
.app .boards__item {
    display: flex;
    flex-direction: column;
    align-self: baseline;
    max-width: 250px;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    margin: 0 15px;
    padding: 10px;
    transition: 0.3s all ease;
    border-radius: 3px;
}
.app .boards__item .title {
    padding: 5px;
    color: white;
}
.app .boards__item .title:focus {
    outline: 1px solid rgba(168, 168, 168, 0.45);
}
.app .boards__item .list {
    min-height: 40px;
}
.app .boards__item .list__item {
    background-color: #fefefe;
    border-radius: 3px;
    padding: 10px;
    text-align: center;
    margin: 4px 0;
    cursor: pointer;
    font-weight: 400px;
    font-size: 14px;
    outline: none;
    border: none;
}
.app .boards__item .form {
    border-radius: 3px;
    margin-top: auto;
    display: none;
}
.app .boards__item .form .textarea {
    resize: none;
    height: 60px;
    border-radius: 3px;
    border: none;
    background: white;
    padding: 10px;
    width: 100%;
}
.app .boards__item .form .textarea:focus {
    outline: none;
}
.app .boards__item .form .buttons {
    display: flex;
}
.app .boards__item .form .buttons .add__item-btn {
    background: rgb(13, 136, 218);
    border: none;
    height: 35px;
    cursor: pointer;
    color: white;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    width: 100%;
}
.app .boards__item .form .buttons .cancel__item-btn {
    padding: 0 10px;
    background: rgb(160, 17, 12);
    border: none;
    height: 35px;
    cursor: pointer;
    color: white;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 75px;
    margin-left: auto;
    flex-shrink: 0;
}
.app .boards__item .add__btn {
    height: 30px;
    background: rgba(155, 155, 155, 0.3);
    border-radius: 3px;
    margin-top: 5px;
    display: flex;
    font-size: 12px;
    align-items: center;
    padding-left: 10px;
    color: white;
    cursor: pointer;
}
.app .boards__item .add__btn span {
    font-size: 20px;
    margin-right: 10px;
    color: white;
}
.app .button {
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(246, 6, 246, 0.963);
    border: none;
    width: 150px;
    cursor: pointer;
    position: absolute;
    bottom: 50px;
    left: 50%;
    transform: translate(-50%);
    color: white;
    border-radius: 3px;
    font-size: 14px;
}
  
.popup-close {
    position: relative;
    right: -24px;
    width: 24px;
    height: 24px;
    padding: 0;
    border: none;
    cursor: pointer;	
    background-image: url("../svg/form-x.svg");
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: center center;
    background-color: red;
    color: black;
    z-index: 999;
}